.container {
  width: 100%;
  min-height: 100vh;
  background-size: cover;
  position: relative; }

.inner-container {
  padding: 70px 60px;
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse; }

.title-description {
  width: 50%;
  color: white; }
  .title-description .title {
    display: inline-block;
    font-size: 36px;
    margin-bottom: 20px; }
  .title-description .description {
    font-size: 18px; }

.image-container {
  position: relative;
  width: 50%; }
  .image-container .image {
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: 80%;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: top; }
  .image-container .hover-background {
    visibility: hidden;
    opacity: 0;
    transition: all 0.25s cubic-bezier(0.42, 0, 0.58, 1);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
  .image-container a.image:hover .hover-background {
    visibility: visible;
    opacity: .5; }
