.container {
  width: 100%;
  height: 100vh;
  padding: 0 20px;
  background: #232323;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 1024px) {
    .container {
      padding: 0 60px; } }

.text-container {
  max-width: 75%;
  text-align: center; }
  @media (min-width: 600px) {
    .text-container {
      max-width: 60%; } }
  .text-container.border {
    border: 1px solid #fff;
    padding: 20px; }

.text {
  font-size: 24px;
  line-height: 1.3;
  color: #fff; }
  @media (min-width: 1024px) {
    .text {
      font-size: 40px; } }
