.container {
  height: 60px;
  margin: 0 20px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (min-width: 1024px) {
    .container {
      height: 75px;
      margin: 0 60px; } }

.logo {
  font-size: 28px;
  font-weight: bold;
  background: #fff;
  padding: 4px 8px; }

.menu-container .button {
  width: 34px;
  height: 34px;
  background: #fff;
  display: flex;
  justify-content: center; }

.menu-container ul.menu {
  margin-top: 30px;
  padding: 12px 10px;
  background: #fff;
  display: none;
  position: absolute;
  right: 0; }
  .menu-container ul.menu.active {
    display: block; }

.menu-container li {
  font-size: 20px; }
  .menu-container li:not(:last-child) {
    margin-bottom: 10px; }
